import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { create } from 'domain';

import { ListarServicoEntregaComponent } from './listar/listar-servico-entrega.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'listar',
    pathMatch: 'full',
  },
  {
    path: '',
    data: {
      title: 'Servico de entrega'
    },
    children: [
      {
        path: 'listar',
        component: ListarServicoEntregaComponent,
        data: {
          title: 'Listar'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ServicoEntregaRoutingModule { }



