import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter, Input } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { FormaPagamentoService } from './../../formapagamento/forma-pagamento.service';

import { ServicoEntregaDTO } from './../../../../../models/domain/servico-entrega.dto';
import { ServicoEntregaService } from './../servico-entrega.service';


@Component({
  selector: 'servico-entrega-popup-modal',
  templateUrl: './servico-entrega-popup-modal.component.html',
  styleUrls: ['./servico-entrega-popup-modal.component.scss']

})
export class ServicoEntregaPopupModalComponent extends AbstractPopupComponent implements OnInit {


  item: ServicoEntregaDTO;


  constructor(
    public servicoService: ServicoEntregaService,
    public fb: FormBuilder) {
    super('servicoEntregaPopup');
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.criarForm();
    this.showPopup("Cadastra serviço de entrega");
  }

  showAlteracao(item: ServicoEntregaDTO) {
    this.alterarForm(item)
    this.showPopup("Alterar serviço de entrega");

  }

  btnConfirmar(item: ServicoEntregaDTO) {
    this.acao(item, this.servicoService);
  }

  criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      nmServicoEntrega: ['', Validators.required],
      nrTelefone: ['', Validators.required],
      emAlteracao: [false]
    })
  }


  alterarForm(item: ServicoEntregaDTO) {
    this.formulario.patchValue({
      id: item.id,
      nmServicoEntrega: item.nmServicoEntrega,
      nrTelefone: item.nrTelefone,
      emAlteracao: true
    })
  }

}



