import { ServicoEntregaDTO } from './../../../../../models/domain/servico-entrega.dto';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';


import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { TrataErrorService } from 'services/trata-error.service';
import { ServicoEntregaService } from './../servico-entrega.service';

@Component({
  templateUrl: './listar-servico-entrega.component.html',
})
export class ListarServicoEntregaComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeSatusModal') popupChangeStatus;

  item: ServicoEntregaDTO;
  items: ServicoEntregaDTO[];

  constructor(
    public servicoService: ServicoEntregaService,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos();
  }

  selecionarItem(item: ServicoEntregaDTO) {
    this.item = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: ServicoEntregaDTO) {
    this.item = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.servicoService.changeStatus(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Servico de Entrega', 'O status da servico de entrega foi alterado com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  excluir() {
    this.servicoService.excluir(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Servico de Entrega', 'O taxa de servico foi excluída com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  private carregarTodos() {
    this.servicoService.findAll().subscribe(response => {
      this.items = response
    }, error => TrataErrorService.tratarError(error));
  }

}



