
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { ModalModule } from 'ng2-bootstrap/modal';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { CurrencyMaskModule } from 'ng2-currency-mask';
import { TextMaskModule } from 'angular2-text-mask';

import { SharedComponentModule } from './../../../shared/shared-component.module';

import { ServicoEntregaService } from './servico-entrega.service';
import { ServicoEntregaPopupModalComponent } from './popup/servico-entrega-popup-modal.component';
import { ListarServicoEntregaComponent } from './listar/listar-servico-entrega.component';
import { ServicoEntregaRoutingModule } from './servico-entrega-routing.module';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    HttpClientModule,
    PopupModalModule,
    TextMaskModule,
    ServicoEntregaRoutingModule
  ],
  declarations: [
    ListarServicoEntregaComponent,
    ServicoEntregaPopupModalComponent
  ],
  providers:[
    ServicoEntregaService
  ]
})
export class ServicoEntregaModule { }



